.contact{
    height: 100vh;
}

.contact-bg{
    width: 20px;
    height: 100%;
    position: absolute;
    background-color: rgb(25, 201, 171);
}

.c-wrapper{
    padding: 50px;
    display: flex;
}

.c-left{
    flex: 1;
}

.c-right{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.c-title{
    font-style: 80px;
    width: 70%;
}

.c-item-info{
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 70%;
}

.c-icon{
    width: 80px;
    height: 80px;
    margin-right: 20px;
}

.c-desc{
    font-weight: 300;
    font-size: 30px;
}

form{
    margin-top: 20px;
}

input{
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-style: 14px;
    padding-left: 10px;
}

textarea{
    width: 100%;
    margin: 10px 0px;
    font-style: 14px;
    padding-left: 10px;
}

button{
    border: none;
    padding: 15px;
    background-color: rgb(25, 201, 171);
    font-weight: 500;
    cursor: pointer;
}

.submit:hover{
    background-color: rgb(11, 100, 85);
    color: white;
}

@media screen and (max-width:480px){
    .c-wrapper{ 
         flex-direction: column;
         padding: 0px 50px;
    }

    .c-title{
        font-size: 30px;
    }

    .c-item-info{
        margin: 20px 0px;
        width: 100%;
    }

    .c-desc{
        display: none;
    }

    form{
        margin-top: 0px;
        display: flex;
        flex-wrap: wrap;
    }

    input{
        width: 35%;
        height: 40px;
        margin: 10px;
        margin-left: 0;
    }

    button{
        margin-top: 10px;
    }
 }